import config from 'react-global-configuration';
import axios from 'axios';



const getQuestionsByQuizId = (quizLink)=>{
    return axios.get(`${config.get('apiUrl')}/get-questions/${quizLink}`);

}
const storeOthersResponse = (data)=>{
    return axios.post(`${config.get('apiUrl')}/other-test`,data);
}

const getResultsByQuizLink = (quizLink)=>{
  return axios.get(`${config.get('apiUrl')}/all-scores?quizLink=${quizLink}`);
}
const getFriendResult = (quizLink, userId)=>{
  return axios.get(`${config.get('apiUrl')}/friend-score?quizLink=${quizLink}&userId=${userId}`);
}
export default {
    getQuestionsByQuizId,
    storeOthersResponse,
    getResultsByQuizLink,
    getFriendResult
}
