import React, { Component } from 'react';
import { Card, CardBody, CardHeader, CardFooter, Col, Container, Row, Table,Button } from 'reactstrap';
import { ItrendLogo } from '../../../containers/DefaultLayout/LogoThemed/ItrendLogo';
import otherQuestionService from '../../../_services/other-question.service';
import { LandingLayout } from '../Landing/components/LandingLayout';


const landingContainer = {
  marginTop: '40px',
  textAlign: 'center'

};

function ResultRow(props) {
  const key = props.index;
  return (
    <tr key={key}>
      <td> {key}</td>
      <td>  {props.userId}</td>
      <td> {props.score}/{props.number_of_questions} </td>

    </tr>
  )
}
class Results extends Component {
  constructor(props) {
    super(props);
    this.state = {
      results: [],
      result: {},
      details:[]
    }
  }
  async getResults() {
    let link = this.props.match.params.link;
    let name = this.props.match.params.name;
    await otherQuestionService.getResultsByQuizLink(link).then(res => {
      otherQuestionService.getFriendResult(link, name).then(res1 => {
        this.setState({
          results: res.data,
          result: res1.data.score,
          details: res1.data.result,
        })
      }).catch(err2 => (
        console.log(err2)));

    }).catch(err => (
      console.log(err)));
  }
  async componentDidMount() {
    await this.getResults();
  }
  getRemark(val){
    if (val===0) {
      return(
        <Button color="danger">Wrong Answer</Button>
      )
    }
    else{
      return(
      <Button color="success">Correct</Button>
      )
    }
  }
  render() {
    const { results, result, details } = this.state;
    return (
      <div>
        <LandingLayout></LandingLayout>
        <Container style={landingContainer} className="center">
          <ItrendLogo checkBackground />
          <Row>
            <Col sm={12}>
              <Card>
                <CardHeader>
                  Results
              </CardHeader>
                <CardBody>
                  <Table responsive hover>
                    <thead>
                      <tr>
                        <th scope="col">id</th>

                        <th scope="col">name</th>
                        <th scope="col"> score</th>


                      </tr>
                    </thead>
                    <tbody>
                      {results.map((result, index) =>
                        <ResultRow key={index} index={index + 1} {...result} />
                      )}
                    </tbody>
                  </Table>
                </CardBody>
                <CardFooter>
                  <Card>
                    <CardBody>
                      <Table responsive hover>
                        <thead>
                          <tr>
                            <th scope="col">id</th>
                            <th scope="col">question</th>
                            <th scope="col"> remark</th>


                          </tr>
                        </thead>
                        <tbody>
                        {
                          details.map((data, index)=>{
                            return (
                          <tr key={index}>
                            <td>{`${index+1}:`}</td>
                            <td><strong>{data.question_text}</strong></td>
                            <td><strong>{data.option}</strong></td>
                            <td>{this.getRemark(data.score)}</td>
                          </tr>
                        )
                          })
                        }
                        </tbody>
                      </Table>
                    </CardBody>
                  </Card>
                  {result.userId}  You Scored {result.score}/{result.number_of_questions}
                </CardFooter>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );

  }
}
export default Results;
